.button {
    height: 30px;
    padding: 5px 25px;
    cursor: pointer;
    color: #fff;
    border-radius: 3px;
    border: none;
    font-weight: 600;
    font-size: 14px;
    font-family: 'Montserrat', sans-serif;
}


.disabled {
    opacity: 0.5;
    background-color: #004D99;
}

.secondary {
    border: 1px solid #0066CC;
    color: #0066CC;
    background-color: transparent;
}

.default {
    background-color: #0066CC;
}

.red {
    background-color: #CC1439;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
    opacity: 0.5;
    cursor: default;
}
